import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import axios from "axios";
import Sidebar from "@/components/Sidebar";
import ProtectedImage from "@/components/ProtectedImage";
import { Icon } from "@iconify/react";
import { motion } from "framer-motion";

const Profile = () => {
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [error, setError] = useState("");

  useEffect(() => {
    const fetchUserProfile = async () => {
      const token = localStorage.getItem("token");

      if (!token) {
        setError("Token tidak ditemukan, silakan login.");
        return;
      }

      try {
        const response = await axios.get("http://localhost:5000/api/profile", {
          headers: { Authorization: `Bearer ${token}` },
        });

        setUser(response.data);
      } catch (err) {
        setError(err.response?.data?.message || "Terjadi kesalahan.");
        localStorage.removeItem("token");
      }
    };

    fetchUserProfile();
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push("/");
  };

  return (
    <div className="bg-black min-h-screen flex">
      <Sidebar href="/history" label="ri:arrow-right-line" />

      <div className="w-full flex flex-col items-center justify-center p-6">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center"
        >
          {/* <ProtectedImage
            src="/branding/logo.png"
            className="w-[96px] mb-4 mx-auto"
            alt="JawirAI Logo"
          /> */}
          <h1 className="text-white text-3xl font-bold mb-2">
            Halo, {user?.name || "Pengguna"}!
          </h1>
          <p className="text-white/35 text-sm max-w-md mx-auto mb-6">
            Terima kasih sudah menggunakan JawirAI. Di bawah ini adalah profil
            akunmu. Jangan lupa tetap berkarya dan bertanya tanpa ragu!
          </p>
        </motion.div>

        {error ? (
          <p className="text-red-500">{error}</p>
        ) : user ? (
          <motion.div
            initial={{ scale: 0.95, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.4, delay: 0.2 }}
            className="backdrop-blur bg-white/5 border border-white/15 shadow-xl rounded-3xl p-6 w-full max-w-sm"
          >
            <div className="flex flex-col items-center space-y-4">
              {/* Avatar Dummy */}
              <motion.div
                whileHover={{ scale: 1.05 }}
                className="w-24 h-24 rounded-full bg-white/10 border border-white/15 flex items-center justify-center text-white text-4xl font-bold"
              >
                {user.name?.charAt(0).toUpperCase()}
              </motion.div>

              {/* Nama */}
              <div className="flex items-center gap-3">
                <Icon
                  icon="gg:profile"
                  className="text-white"
                  width={20}
                  height={20}
                />
                <p className="text-white/35 text-sm">{user.name}</p>
              </div>

              {/* Email */}
              <div className="flex items-center gap-3">
                <Icon
                  icon="ic:baseline-email"
                  className="text-white"
                  width={20}
                  height={20}
                />
                <p className="text-white/35 text-sm">{user.email}</p>
              </div>

              {/* Dummy Status */}
              <div className="mt-4 bg-white/10 text-white/45 px-3 py-1 rounded-full text-xs">
                Status: <span className="text-white/75">Aktif</span> • Pengguna
                Reguler
              </div>

              {/* Tombol Logout */}
              <motion.button
                whileTap={{ scale: 0.95 }}
                whileHover={{ scale: 1.02 }}
                className="mt-5 w-full py-2 bg-red-500 hover:bg-red-600 transition-all rounded-full text-white/75 text-sm"
                onClick={handleLogout}
              >
                Logout
              </motion.button>
            </div>
          </motion.div>
        ) : (
          <p className="text-white mt-5">Loading...</p>
        )}
      </div>
    </div>
  );
};

export default Profile;
